/* Additional styles beyond global styles.scss */
/* Card image hover effects */
.bg-gray-100 img {
  transition: transform 0.3s ease-in-out;
}

.bg-gray-100:hover img {
  transform: scale(1.05);
}

/* Button hover effects - more specific to override Tailwind inline styles */
.btn {
  transition: all 0.2s ease-in-out !important;
}

/* Primary button hover styles */
.btn-primary:hover {
  background-color: var(--arabic-green-dark) !important;
  color: white !important;
}

/* Outline button hover styles */
.btn-outline:hover {
  background-color: var(--arabic-green) !important;
  color: white !important;
}

/* Feature card hover effects */
.bg-gray-50:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.bg-gray-50 a {
  transition: color 0.2s ease-in-out;
}
.bg-gray-50 a:hover {
  color: var(--arabic-green-dark);
}

/* Pagination hover effects */
button.relative {
  transition: all 0.2s ease-in-out;
}
button.relative:hover:not([disabled]) {
  color: var(--arabic-green);
}

/* Animations for loading states */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* QR code print styles */
@media print {
  .qr-print-section {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding: 2rem;
    background-color: white;
  }
  .qr-print-section img {
    display: block;
    margin: 0 auto;
    max-width: 300px;
  }
  .qr-print-section h3 {
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
  .qr-print-section p {
    text-align: center;
    font-size: 0.875rem;
    color: var(--gray-500);
  }
}
/* Responsive adjustments for small screens */
@media (max-width: 640px) {
  .btn {
    width: 100%;
    justify-content: center;
  }
  h1 {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
  h2 {
    font-size: 1.5rem;
    line-height: 2rem;
  }
}
/* Fix for Safari browser */
@supports (-webkit-touch-callout: none) {
  .bg-gray-100 img {
    height: 100%;
    object-position: center;
  }
}
/* RTL overrides for Arabic */
[dir=rtl] .rtl {
  font-family: var(--font-tajawal);
}

/* Dark mode support */
:host-context(.dark-theme) {
  /* Dark mode hover effects */
}
:host-context(.dark-theme) .bg-white {
  background-color: var(--gray-800);
}
:host-context(.dark-theme) .text-gray-900 {
  color: var(--gray-100);
}
:host-context(.dark-theme) .text-gray-600 {
  color: var(--gray-300);
}
:host-context(.dark-theme) .text-gray-500 {
  color: var(--gray-400);
}
:host-context(.dark-theme) .border-gray-200 {
  border-color: var(--gray-700);
}
:host-context(.dark-theme) a:hover {
  color: var(--arabic-green-light);
}
:host-context(.dark-theme) .btn-outline:hover {
  background-color: var(--arabic-green) !important;
  color: white !important;
}
:host-context(.dark-theme) .btn-primary:hover {
  background-color: var(--arabic-green-dark) !important;
}

/* RTL Support */
[dir=rtl] .rtl\:rotate-180 {
  transform: rotate(180deg);
}
